import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
	const { data: btnUtils } = useWPOptionQuery('apbUtils');
	const { data = null, refetch, isLoading = true } = useWPAjax('apbPipeChecker', { _wpnonce: btnUtils?.nonce }, true);
	const isPremium = (!isLoading && data?.isPipe) || false;

	useEffect(() => {
		refetch()
	}, [btnUtils]);

	return { isPremium, isLoading };
};
export default usePremiumInEditor;